* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Merriweather Sans", sans-serif;
}

.app {
  background-image: url("https://images.hdqwalls.com/download/wolf-cave-minimalist-4k-s1-2048x1152.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease;
}
@media screen and (max-width: 600px) {
  .app {
    background-image: url("https://images.hdqwalls.com/download/wolf-cave-minimalist-4k-s1-2160x3840.jpg");
  }
}
@media screen and (max-width: 600px) {
  .app.warm {
    background-image: url("https://images.hdqwalls.com/download/farm-field-flora-flowers-scenery-5k-yb-2160x3840.jpg");
  }
}

.app.warm {
  background-image: url("https://images.hdqwalls.com/download/farm-field-flora-flowers-scenery-5k-yb-3840x2160.jpg");
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  padding: 25px;
}

.search-box {
  width: 50%;
  margin: 0 0 75px;
  margin-left: 27%;
}
@media screen and (max-width: 400px) {
  .search-box {
    width: 100%;
    margin: 0 0 75px;
  }
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px 16px 16px 16px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #fff;
  font-size: 39px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  margin-top: -85px;
  position: relative;
  display: inline-block;

  border-radius: 20px;

  padding: 100px 130px 13px 100px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
@media screen and (max-width: 600px) {
  .weather-box .temp {
    position: relative;
    display: inline-block;
    margin: 4px auto;

    border-radius: 20px;

    padding: 15px 25px;

    color: #fff;
    font-size: 102px;
    font-weight: 900;

    text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
    text-align: center;
  }
}
